.header{
    height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header_logo{
    width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
  height: 100px;
}

.header_search{
    display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;

}
.headerSearchInput{
    height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}
.header_searchIcon{
    padding: 5px;
    height: 22px !important;
    background-color: #ff7322;

}
.header_nav{
    display: flex;
    justify-content: space-evenly;
}
.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}
.shoppingBaketIcon{
    display: flex;
    align-items: center;
    color: white;
}
.headerBasketCount{
    margin-left: 10px;
    margin-right: 10px;
}
.headerOptionOne{
    font-size: 10px;
}
.headerOptionTwo{
    font-size: 13px;
    font-weight: 800;
}

